/* Reset
 */
a, button {
  cursor: revert;
}

button {
  border: none;
  background-color: transparent;
}

strong {
  font-weight: 700;
}

ol, ul {
  list-style-type: none;
}

img {
  max-width: 100%;
}

table {
  border-collapse: collapse;
}

input, textarea {
  -webkit-user-select: auto;
}

textarea {
  white-space: revert;
}

:where([hidden]) {
  display: none;
}

/* Base
 */
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  color: inherit;
}

body {
  line-height: 1.5;
  font-family: "Inter", sans-serif;
  font-size: 0.9rem;
  font-weight: 200;
}

/* Typography
 */
table, th, .th, caption, .caption, p, ol, ul {
  font-size: 0.875rem;
  font-weight: 200;
  margin-bottom: 1rem;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  font-weight: 700;
  margin-bottom: 1rem;
  letter-spacing: -0.075pt;
}

p li, ol li, ul li {
  font-size: inherit;
  font-weight: inherit;
}

h1, .h1 {
  font-size: 2rem;
}

h2, .h2 {
