/* header */
@media screen and (min-width: 768px) {
  .header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 3px 3px rgba(0, 0, 0, 0.1);
  }
}

.header__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 900;
  letter-spacing: -2px;
}
.header__title a {
  font-weight: 900;
  letter-spacing: -2px;
}

/* navigation */
.header__menu-btn {
  background-color: #002f35;
  color: #f2f5f5;
  border-radius: 4px;
  font-size: 0.85rem;
  line-height: 1;
  width: 35px;
  height: 35px;
  white-space: nowrap;
  cursor: pointer;
  vertical-align: baseline;
  z-index: 11;
}
@media screen and (min-width: 768px) {
  .header__menu-btn {
    display: none;
  }
}

.top-navigation {
  display: none;
}
@media screen and (min-width: 768px) {
  .top-navigation {
    display: block;
  }
}

.top-navigation.open {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding-left: 25%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.7);
}
@media screen and (min-width: 768px) {
  .top-navigation.open {
    position: relative;
    top: auto;
    right: auto;
    left: auto;
    padding-left: 0;
    height: auto;
  }
}

.open .nav__list {
  background-color: white;
  height: 100%;
  box-shadow: -2px 0 3px 10px rgba(0, 0, 0, 0.1);
  padding-top: 50px;
  padding-left: 15px;
  padding-right: 15px;
}
@media screen and (min-width: 768px) {
  .open .nav__list {
    padding: 0;
    box-shadow: none;
  }
}
.open .nav__item {
  font-size: 1.25rem;
  line-height: 2;
}

@media screen and (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;
  }
}

